import './App.css'
import { useState, useEffect, useRef } from 'react'
import Switch from 'react-switch'
import RangeSlider from 'react-range-slider-input'
import 'react-range-slider-input/dist/style.css'
import 'react-clock/dist/Clock.css'

import Clock from 'react-clock'

import { database } from './data'
import { ref, set } from 'firebase/database'

import logo from './logo.png'

import { Button, Checkbox } from '@material-tailwind/react'

import { Alert } from '@material-tailwind/react'
import { ReactComponent as ButtonImage } from './assets/on-button-svgrepo-com.svg'
import { ReactComponent as BgImage } from './assets/electricity-energy-plug-power-technology-svgrepo-com.svg'

function App() {
  const [voltage, setVoltage] = useState(0)
  const [current, setCurrent] = useState(0)
  const [power, setPower] = useState(0)
  const [checkedI, setCheckedI] = useState(true)
  const [checkedC, setCheckedC] = useState(true)
  const [checkedR, setCheckedR] = useState(true)
  const [gridColor, setGridColor] = useState('blue')

  const [value, setValue] = useState(new Date())

  const [rationTimesI, setRationI] = useState({
    startTime: 0,
    stopTime: 24,
  })

  const [rationTimesR, setRationR] = useState({
    startTime: 0,
    stopTime: 24,
  })

  const [rationTimesC, setRationC] = useState({
    startTime: 0,
    stopTime: 24,
  })

  const [rationState, setRationState] = useState(true)
  const [btnText, setBtnText] = useState('Enable Rationing')

  const [checkRationState, setCheckRationState] = useState('off')

  useEffect(() => {
    const interval = setInterval(() => setValue(new Date()), 1000)

    return () => {
      clearInterval(interval)
    }
  }, [])

  function handleChangeI(checkedI) {
    setCheckedI(checkedI)
  }

  function handleChangeC(checkedC) {
    setCheckedC(checkedC)
  }

  function handleChangeR(checkedR) {
    setCheckedR(checkedR)
  }

  useEffect(() => {
    if (checkedI) {
      // console.log(database)
      set(ref(database, 'industrial'), {
        industrial: 'ON',
      })
    } else {
      set(ref(database, 'industrial'), {
        industrial: 'OFF',
      })
    }

    if (checkedR) {
      // console.log(database)
      set(ref(database, 'residential'), {
        residential: 'ON',
      })
    } else {
      set(ref(database, 'residential'), {
        residential: 'OFF',
      })
    }

    if (checkedC) {
      set(ref(database, 'commercial'), {
        commercial: 'ON',
      })
    } else {
      set(ref(database, 'commercial'), {
        commercial: 'OFF',
      })
    }
  }, [checkedI, checkedC, checkedR])

  const Ichange = (val) => {
    setRationI({ startTime: val[0], stopTime: val[1] })
  }
  const Rchange = (val) => {
    setRationR({ startTime: val[0], stopTime: val[1] })
  }
  const Cchange = (val) => {
    setRationC({ startTime: val[0], stopTime: val[1] })
  }

  function toggleRation() {
    if (rationState === true) {
      setRationState(false)
      setBtnText('Disable Rationing')
    }

    if (rationState === false) {
      setRationState(true)
      setBtnText('Enable Rationing')
    }
  }

  const currentTime = new Date()

  function enforeRationing() {
    if (rationState === false) {
      if (
        currentTime.getHours() > rationTimesC.startTime &&
        currentTime.getHours() < rationTimesC.stopTime
      ) {
        console.log('commercial section on')
        set(ref(database, 'commercial'), {
          commercial: 'ON',
        }).then(() => {
          setCheckedC(true)
        })
      } else {
        console.log('commercial section off')
        set(ref(database, 'commercial'), {
          commercial: 'OFF',
        }).then(() => {
          setCheckedC(false)
        })
      }

      if (
        currentTime.getHours() > rationTimesI.startTime &&
        currentTime.getHours() < rationTimesI.stopTime
      ) {
        console.log('industrial section on')

        set(ref(database, 'industrial'), {
          industrial: 'ON',
        }).then(() => {
          setCheckedI(true)
        })
      } else {
        console.log('industrial section off')
        set(ref(database, 'industrial'), {
          industrial: 'OFF',
        }).then(() => {
          setCheckedI(false)
        })
      }

      if (
        currentTime.getHours() > rationTimesR.startTime &&
        currentTime.getHours() < rationTimesR.stopTime
      ) {
        console.log('residential section on')
        set(ref(database, 'residential'), {
          residential: 'ON',
        }).then(() => {
          setCheckedR(true)
        })
      } else {
        console.log('residential section off')
        set(ref(database, 'residential'), {
          residential: 'OFF',
        }).then(() => {
          setCheckedR(false)
        })
      }
    }
  }

  // this is the shitzzz
  function check4checked(event) {
    let ans = event.target.value

    setCheckRationState(ans)
  }

  useEffect(() => {
    if (checkRationState === 'on') {
      enforeRationing()
      console.log('hours just changed')
    }
  }, [currentTime.getHours()])

  function shutDown() {
    console.log('shutting down')

    setCheckedI(false)
    setCheckedR(false)
    setCheckedC(false)
  }

  return (
    <div className="main-container min-w-[368px] h-[100%]  w-[100vw] sm:h-[100dvh] flex flex-col items-center justify-center bg-[#0F172A] text-white text-sm text-font-popins text-[92%]">
      <div className="text-[80%]   sm:text-[100%] items-center text-center">
        IOT-BASED SMART GRID FOR ENHANCED ENERGY EFFICIENCY AND MANAGEMENT
      </div>
      <div className="text-[70%]  ">supervised by Dr Aliyu Sabo</div>
      <div className="content w-[80%] h-[90%] flex  shadow-lg flex-col items-center  bg-[#00000064] rounded-2xl sm:p-2">
        <div className="flex   w-[100%] items-center instrumentation bg-slate-500 rounded-2xl">
          <img src={logo} alt="Logo" className="w-[90px] h-[90px]  p-2 " />
          <div className="grid  grid-cols-3   lg:ml-[18%] gap-4 p-2 text-center lg:w-[50%] w-[100%] h-[80%] instrumentation bg-[#d3d4d32a] shadow-md rounded-2xl  border border-solid border-[#d3d4d37f]">
            <div>Voltage</div>
            <div>Current </div>
            <div>Power </div>

            <div>{voltage} volt</div>
            <div>{current} amp </div>
            <div>{power} watt</div>
          </div>
        </div>

        <div className="controls  bg-[#0e243746] p-2 sm:h-[50%]  w-full sm:w-[100%]  rounded-2xl mt-2 sm:flex justify-evenly  gap-5 ">
          <div className="text-[90%] sm:w-[16%] sm:h-full bg-slate-900 rounded-xl flex flex-row  sm:flex sm:flex-col justify-center items-center sm:gap-[6%]">
            <div className="hidden sm:block">Industrial</div>
            <Switch
              name="i"
              id="ind"
              onChange={handleChangeI}
              checked={checkedI}
              borderRadius={2}
            />

            <div className="hidden sm:block">Commercial</div>

            <Switch
              id="Com"
              onChange={handleChangeC}
              checked={checkedC}
              borderRadius={2}
            />
            <div className="hidden sm:block">Residential</div>

            <Switch
              id="residential"
              onChange={handleChangeR}
              checked={checkedR}
              borderRadius={2}
            />
          </div>

          <div className="reasons w:[100%] p-2 text-[90%]  sm:w-[60%] h-[full] bg-slate-900 rounded-xl flex flex-col gap-2 justify-evenly place-items-center">
            <div className="bg-[#10263d93] sm:w-[80%] h-1/4  border border-solid border-gray-800 rounded-xl p-2  flex items-center">
              Commercial area consumes - {'600KW'} killowatts last shutdown -
              {'20PM'} , current state{' '}
              {(function () {
                if (checkedC === true) {
                  return 'ON'
                } else {
                  return 'OFF'
                }
              })()}
            </div>
            <div className="bg-[#10263d93] sm:w-[80%] h-1/4  border border-solid border-gray-800 rounded-xl p-2  flex items-center">
              Industrial area consumes - {'900KW'} killowatts last shutdown -
              {'20PM'} , current state{' '}
              {(function () {
                if (checkedI === true) {
                  return 'ON'
                } else {
                  return 'OFF'
                }
              })()}
            </div>
            <div className="bg-[#10263d93] sm:w-[80%] h-1/4  border border-solid border-gray-800 rounded-xl p-2 flex items-center">
              Residential area consumes - {'80KW'} killowatts last shutdown -
              {'20PM'} , current state{' '}
              {(function () {
                if (checkedR === true) {
                  return 'ON'
                } else {
                  return 'OFF'
                }
              })()}
            </div>
          </div>
          <div className="sm:w-[16%] sm:h-full   shadow-md rounded-xl  gap-1   flex sm:flex-col items-center justify-center">
            <div className="text-[80%]">Shut Down </div>
            <image src={ButtonImage} width={80} height={80} />

            <Button
              onClick={shutDown}
              variant="outlined"
              className="rounded-full flex items-center justify-center sm:w-[30%] sm:h-[20%] text-white bg-gradient-to-r from-teal-400 via-teal-500 to-teal-600 hover:bg-gradient-to-br focus:ring-4 focus:ring-teal-300 dark:focus:ring-teal-800 font-medium text-sm px-5 py-2.5 text-center"
              ripple={true}
              color="amber"
            >
              <BgImage className="absolute hidden sm:block" />
            </Button>
          </div>
        </div>

        <div className="fillers   bg-[#5c697446]  controls bg-slate-600 p-5 sm:h-[35%] sm:w-[100%] rounded-2xl mt-2 sm:flex gap-5  ">
          <div className="clock area  w-[20%] sm:h-full bg-slate-500 rounded-xl  flex flex-col items-center justify-center">
            <div className="hidden sm:block">
              <Clock
                value={value}
                size={100}
                className="text-white bg-[#ffffff61] rounded-xl"
                color={'white'}
              />
              <div className="flex flex-col mt-3 text-xs ration-times">
                <div>
                  industrial {rationTimesI.startTime} -{rationTimesI.stopTime}{' '}
                  hrs
                </div>
                <div>
                  commercial {rationTimesC.startTime} -{rationTimesC.stopTime}{' '}
                  hrs
                </div>
                <div>
                  Residential {rationTimesR.startTime} -{rationTimesR.stopTime}{' '}
                  hrs
                </div>
              </div>
            </div>
          </div>
          <div className="rationing area  sm:w-[56%] sm:h-full bg-slate-900 rounded-xl flex flex-col gap-[10px]  sm:p-5 text-[80%]">
            <div>Industrial</div>

            <RangeSlider
              min={0}
              max={24}
              onInput={Ichange}
              disabled={rationState}
            />
            <div>Commercial</div>
            <RangeSlider
              min={0}
              max={24}
              onInput={Cchange}
              disabled={rationState}
            />
            <div>Residential</div>
            <RangeSlider
              min={0}
              max={24}
              onInput={Rchange}
              disabled={rationState}
            />
          </div>

          <div className="clock area  sm:w-[20%] sm:h-full bg-slate-500 rounded-xl  flex flex-col items-center justify-center sm:mt-[0] mt-5 gap-5">
            <button
              onClick={toggleRation}
              className="w-[50%] hidden h-[20%] bg-blue-600 rounded-md shadow-black shadow-sm hover:bg-blue-700"
            >
              {btnText}
            </button>
            <button
              className="w-[50%] hidden h-[20%] bg-blue-600 rounded-md shadow-black shadow-sm hover:bg-blue-700"
              onClick={enforeRationing}
            >
              enforce
            </button>
            <Button fullWidth={true} color="blue" onClick={toggleRation}>
              {btnText}
            </Button>
            <Button
              variant="filled"
              color="blue"
              fullWidth={true}
              onClick={enforeRationing}
              disabled={rationState}
            >
              Enforce NOW
            </Button>

            <Checkbox
              label="time based rationing"
              onChange={check4checked}
              disabled={rationState}
            />
          </div>
        </div>
      </div>
      <div className="text-[70%]  sm:text-[80%] items-center text-center">
        desiged by : Jatau Nichlas Daniel , Engr Hamza Ozovehe Sulieman, Dahiru
        Yakubu, Ashaheme Temple Chikodi
      </div>
      <div className="text-[70%] hidden  sm:text-[80%] items-center text-center">
        Electrical/Electronics Department NDA.
      </div>
    </div>
  )
}

export default App
